import { usersAPI, profileAPI } from "../API/API"

const ADD_POST = 'ADD_POST'
const SET_USER_PROFILE = 'SET_USER_PROFILE'
const SET_STATUS = 'SET_STATUS'
const DELETE_POST = 'DELETE_POST'

let initialState = {
    posts: [
        { id: '1', message: 'Go', likesCount: 18 },
        { id: '2', message: 'Super', likesCount: 105 },
        { id: '3', message: "It's my first post!", likesCount: 15 },
    ],
    profile: null,
    status: ''
}

const profileReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_POST: {
            let newPost = {
                id: 4,
                message: action.newPostElement,
                likesCount: 0
            };
            return {
                ...state,
                posts: [...state.posts, newPost],
                newPostElement: ''

            }
        }

        case SET_STATUS: {
            return {
                ...state,
                status: action.status,
            }
        }

        case SET_USER_PROFILE: {
            return { ...state, profile: action.profile }
        }

        case DELETE_POST: {
            return { ...state, posts: state.posts.filter(p => p.id != action.postId) }
        }

        default:
            return state;
    }
}

export const addPostActionCreator = (newPostElement) => ({
    type: 'ADD_POST', newPostElement
})

export const setUserProfile = (profile) => ({
    type: 'SET_USER_PROFILE', profile
})

export const setStatus = (status) => ({
    type: 'SET_STATUS', status
})

export const deletePost = (postId) => ({
    type: 'DELETE_POST', postId
})

export const getUserProfile = (userId) => async (dispatch) => {
    let response = await usersAPI.getProfile(userId)
    dispatch(setUserProfile(response.data));
}

export const getStatus = (userId) => async (dispatch) => {
    let response = await profileAPI.getStatus(userId)
    dispatch(setStatus(response.data));
}

export const updateStatus = (status) => async (dispatch) => {
    let response = await profileAPI.updateStatus(status)
    if (response.data.resultCode === 0) {
        dispatch(setStatus(status));
    }
}

export default profileReducer